/**
 * Base: Fonts
 *
 * Only load WOFF2 & WOFF
 * Remember to preload WOFF2 in HTML head, like this:
 * <link rel="preload" href="/dist/fonts/MyFontNameHere.woff2" as="font">
 * font-display explanation: https://css-tricks.com/font-display-masses/
 */

@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;1,100;1,300;1,400;1,500;1,600;1,700&display=swap');





@font-face {
    font-family:"Gazpacho";
    src:
    url("/wp-content/themes/kindtand/__dist/fonts/Gazpacho-Bold.otf") format("opentype"),
    url("/wp-content/themes/kindtand/__dist/fonts/Gazpacho-Bold.woff") format("woff"),
    url("/wp-content/themes/kindtand/__dist/fonts/Gazpacho-Bold.woff2") format("woff2");
    font-display:auto;font-style:normal;font-weight:700;
}
